import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import { TextField } from "@material-ui/core";
import MenuItem from '@material-ui/core/MenuItem';
import CardFooter from "components/Card/CardFooter.js";
import Button from "components/CustomButtons/Button.js";

const styles = {
  cardCategoryWhite: {
    "&,& a,& a:hover,& a:focus": {
      color: "rgba(255,255,255,.62)",
      margin: "0",
      fontSize: "14px",
      marginTop: "0",
      marginBottom: "0"
    },
    "& a,& a:hover,& a:focus": {
      color: "#FFFFFF"
    }
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  }
};

const waves = [
    {
      value: 'wave1',
      label: 'Wave 1',
    },
    {
      value: 'wave2',
      label: 'Wave 2',
    },
    {
      value: 'wave3',
      label: 'Wave 3',
    },
    {
    value: 'wave4',
    label: 'Wave 4',
    },
    {
    value: 'wave5',
    label: 'Wave 5',
    },
    {
    value: 'wave6',
    label: 'Wave 6',
    },
    {
    value: 'wave7',
    label: 'Wave 7',
    },
    {
    value: 'wave8',
    label: 'Wave 8',
    },
  ];

  const horarios = [
    {
      value: 'hora1',
      label: '08:00 AM',
    },
    {
      value: 'hora2',
      label: '09:00 AM',
    },
    {
      value: 'hora3',
      label: '10:00 AM',
    },
    {
    value: 'hora4',
    label: '11:00 AM',
    },
    {
    value: 'hora5',
    label: '12:00 PM',
    },
    {
    value: 'hora6',
    label: '13:00 PM',
    },
    {
    value: 'hora7',
    label: '14:00 PM',
    },
    {
    value: 'hora8',
    label: '15:00 PM',
    },
  ];

const useStyles = makeStyles(styles);

export default function TableList() {
  const classes = useStyles();
  const [wave, setWave] = React.useState('wave1');
  const [horario, setHorario] = React.useState('hora1');
  
  const handleWaveChange = (event) => {
    setWave(event.target.value);
  };

  const handleHorarioChange = (event) => {
    setHorario(event.target.value);
  };

  return (
    <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="primary">
            <h4 className={classes.cardTitleWhite}>Schedule </h4>
          </CardHeader>
          <CardBody>
            <GridContainer>
            <GridItem xs={6} sm={12} md={8}>
                <TextField className={classes.inputModulo}
                select
                label="Wave"
                value={wave}
                onChange={handleWaveChange}
                >
                {waves.map((option) => (
                <MenuItem 
                key={option.value} 
                value={option.value}>
                {option.label}
                </MenuItem>
                ))}
                </TextField>
            </GridItem>
            <GridItem xs={6} sm={12} md={2}>
                <TextField className={classes.inputModulo}
                select
                label="Horario"
                value={horario}
                onChange={handleHorarioChange}
                >
                {horarios.map((option) => (
                <MenuItem 
                key={option.value} 
                value={option.value}>
                {option.label}
                </MenuItem>
                ))}
                </TextField>
            </GridItem>
            </GridContainer>
          </CardBody>
          <CardFooter>
              <Button color="primary">Salvar</Button>
              <Button color="primary">Fechar</Button>
            </CardFooter>
        </Card>
      </GridItem>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
          <CardHeader color="primary">
            <h4 className={classes.cardTitleWhite}>Status</h4>
          </CardHeader>
          <CardBody>
            <Table
              tableHeaderColor="primary"
              tableHead={["Wave", "Data", "Horário", "Situação"]}
              tableData={[ 
                ["Wave 1", "18/11/2020", "08:00 AM", "Finalizado"],
                ["Wave 1", "18/11/2020", "09:00 AM", "Finalizado"],
                ["Wave 1", "18/11/2020", "12:00 PM", "Finalizado"],
                ["Wave 1", "18/11/2020", "14:00 PM", "Novo"],
                ["Wave 1", "18/11/2020", "16:00 PM", "Novo"],
                ["Wave 1", "18/11/2020", "17:00 PM", "Novo"],
                ["Wave 1", "18/11/2020", "18:00 PM", "Novo"],
              ]}
            />
          </CardBody>
        </Card>
      </GridItem>
    </GridContainer>
    
  ); 
}
